<template>
	<Tabs value="system" class="tab">
		<Tab-pane label="权限编辑" name="system" class="tab-item">
			<Tree :data="baseData" show-checkbox ref="tree"></Tree>
			<div class="btn-cell">
				<i-button type="primary" class="sub" size="large" @click="save('tree')">保存</i-button>
				<i-button type="ghost" class="cancel" size="large" @click="cancel">取消</i-button>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				baseData: [],
				privilege: [],
				roleId: Number,
			}
		},
		created: function () {
			this.loadList();
		},
		methods: {
			loadList: function () {
				let _this = this;
				let params = _this.$route.params
				_this.$http.post('/auth/privilegeList', {
					roleId: params.roleId
				})
					.then(function (response) {
						let res = response.data;
						if(res.data){
							_this.privilege = _.map(res.data, 'privilegeId');
							_this.roleId = params.roleId;
							_this.baseData = [
								{
									expand: true,
									title: params.roleName,
									disableCheckbox: true,
									children: [
										{
											title: '贷款审批',
											privilegeId: 1000,
											expand: true,
											children: [
												{
													title: '金融专员',
													privilegeId: 1080,
													checked: _.includes(_this.privilege, 1080)
												},
												{
													title: '授信初审',
													privilegeId: 1001,
													checked: _.includes(_this.privilege, 1001)
												},
												{
													title: '授信复审',
													privilegeId: 1002,
													checked: _.includes(_this.privilege, 1002)
												},
												{
													title: '放款初审',
													privilegeId: 1003,
													checked: _.includes(_this.privilege, 1003)
												},
												{
													title: '放款复审',
													privilegeId: 1004,
													checked: _.includes(_this.privilege, 1004)
												},
												{
													title: '放款终审',
													privilegeId: 1005,
													checked: _.includes(_this.privilege, 1005)
												},
												{
													title: '账务确认',
													privilegeId: 1006,
													checked: _.includes(_this.privilege, 1006)
												},
											]
										},
										{
											title: '贷后管理',
											privilegeId: 2000,
											expand: true,
											children: [
												{
													title: '在贷客户',
													privilegeId: 2001,
													checked: _.includes(_this.privilege, 2001)
												},
												{
													title: '清贷客户',
													privilegeId: 2002,
													checked: _.includes(_this.privilege, 2002)
												}
											]
										},
										{
											title: '押品管理',
											privilegeId: 3000,
											expand: true,
											children: [
												{
													title: '入库',
													privilegeId: 3001,
													checked: _.includes(_this.privilege, 3001)
												},
												{
													title: '出库',
													privilegeId: 3002,
													checked: _.includes(_this.privilege, 3002)
												},
												{
													title: '出入审批',
													privilegeId: 3003,
													checked: _.includes(_this.privilege, 3003)
												}
											]
										},
										{
											title: '统计报表',
											expand: true,
											privilegeId: 4000,
											children: [
												{
													title: '进件统计',
													privilegeId: 4001,
													checked: _.includes(_this.privilege, 4001)
												},
												{
													title: '放款统计',
													privilegeId: 4002,
													checked: _.includes(_this.privilege, 4002)
												},
												{
													title: '台账报表',
													privilegeId: 4003,
													checked: _.includes(_this.privilege, 4003)
												}
											]
										},
										{
											title: '我的消息',
											disabled: true,
											privilegeId: 5000,
											checked: true
										},
										{
											title: '系统管理',
											privilegeId: 6000,
											checked: _.includes(_this.privilege, 6000)
										}
									]
								}
							];
						}
					})
					.catch(function (error) {
						//处理错误
						_this.$Message.error({
							content: error,
							duration: 3
						});
					});
			},
			save: function (name) {
				let _this = this;
				let checkedNode = this.$refs[name].getCheckedNodes();
				let params = {
					roleId: _this.roleId,
					privilegeId: _.map(_.filter(checkedNode, 'privilegeId'), 'privilegeId')
				};
				_this.$http.post('/auth/modifyURolePrivilege', params)
					.then(function (response) {
						//处理回调
						if (response.data.code == 0) {//赋权失败
							_this.$Message.error({
								content: response.data.msg,
								duration: 3
							});
						} else if (response.data.code == 1) {
							_this.$Message.success({
								content: response.data.msg
							});
							_this.$router.replace('/editrole');
						}
					})
					.catch(function (error) {
						//处理错误
						_this.$Message.error({
							content: error,
							duration: 3
						});
					})
			},
			cancel: function () {
				this.$router.go(-1);
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		font-size: 14px;
		color: #20abff;
	}

	.btn-cell {
		padding-top: 30px;
	}

	.sub {
		width: 200px;
		height: 50px;
		margin-right: 30px;
		background-color: #20abff;
		border-color: #20abff;
		font-size: 18px;
		letter-spacing: 5px;
	}

	.cancel {
		width: 200px;
		height: 50px;
		border-color: #20abff;
		color: #20abff;
		font-size: 18px;
		letter-spacing: 5px;
	}
</style>



